import React ,{useState,useEffect}from 'react'
import './App.css';
import {  FormControl , Input } from '@material-ui/core';
import Message from './Components/Message';
import db from './firebase';
import firebase from 'firebase';
import FlipMove from 'react-flip-move';
import image from './Components/images/image.jpg';
import SendIcon from '@material-ui/icons/Send';
import { IconButton } from '@material-ui/core';


function App() {
  const [input,setInput] = useState('');
  const [messages,setMessages] = useState ([  
    {username: 'sweety',message:'hey guys'},
    {username: 'bunny' , message: 'whats up'}  
  ]);
  const [username,setUsername] = useState('');

  useEffect(()=> {
    // run once when the app component loads
    db.collection('messages')
    .orderBy('timestamp','desc')
    .onSnapshot(snapshot => {
     setMessages(snapshot.docs.map(doc => ({id: doc.id , message:doc.data()}))) 
    })
  } ,[] )

  
  useEffect(() =>{
   setUsername(prompt('please enter your name'))

  },[]) //condition

  const sendMessage = (event) => {
    event.preventDefault();
    // logic for sending messages
    /*setMessages([
      ...messages,{username:username,text:input}
    ]); */

    db.collection('messages').add({
      message: input,
      username: username,
      timestamp: firebase.firestore.FieldValue.serverTimestamp()

    })
    setInput('');
  }

  return (
    <div className="App">
      <img src={image} width="150" height="100" alt="messenger"></img>
      <h1>Hey Welcome </h1>
      <h2>Welcome {username}</h2>
      <form className="app__form">
      <FormControl className="app__formControl">
         
         <Input className="app__input"  placeholder='Enter message ....' value={input} onChange={event => setInput(event.target.value)} />
        <IconButton className="app__iconButton" disabled={!input} variant="contained" color="primary" type='submit' onClick={sendMessage}>
          <SendIcon/>
        </IconButton>
      </FormControl>
      </form>
      {/*messages themselves */}
      <FlipMove>
      {
        messages.map(({id,message}) =>(
          <Message key={id} username={username} message={message}/>
        ))
      }
      </FlipMove>
    </div>
  );
}

export default App;
